  methods: {

    changeN(a) {
      this.$store.commit('changeName', a)
    }
    ,
    getData() {
      axios({
        url: 'http://127.0.0.1:8000/api/member/',
        method: 'get',
        /*
        transformRequest: data => {

        },
        headers: {
          'xRequested-With': 'XMLHttpRequest',
        },
        params: {
          id: 1
        }
        */
      }).then(res => {
        console.log(res)
      }).catch(err => {
        console.log(err)
      })
    }
  }
}
</script>

